import React, { useState, FC } from 'react';
import PropTypes from 'prop-types';
import clsx from 'clsx';
import {
  Box,
  IconButton,
  Tooltip,
  Typography,
  colors,
  makeStyles
} from '@material-ui/core';
import BookmarkBorderOutlinedIcon from '@material-ui/icons/BookmarkBorderOutlined';
import ShareIcon from '@material-ui/icons/Share';
import CustomIcon from 'src/components/CustomIcon';
import { MessageCircle as MessageCircleIcon } from 'react-feather';
import { Post } from 'src/types/post';
import { Theme } from 'src/theme';

interface ReactionsProps {
  className?: string;
  post: Post;
}

const useStyles = makeStyles((theme: Theme) => ({
  root: {
    display: 'flex',
    alignItems: 'center'
  },
  div: {
    flexDirection: 'column',
    position: 'fixed',
    top: '45vh',
    left: '10vw'
  },
  likedButton: {
    color: colors.red[600]
  },
  likedIcon: {
    padding: theme.spacing(1)
  },
  commentBox: {
    marginTop: theme.spacing(3),
    [theme.breakpoints.down('sm')]: {
      marginTop: theme.typography.pxToRem(6),
      marginLeft: theme.spacing(2),
      marginRight: theme.spacing(1)
    }
  },
  iconBox: {
    marginTop: theme.spacing(1),
    [theme.breakpoints.down('sm')]: {
      marginTop: theme.typography.pxToRem(2),
    }
  },
  commentIcon: {
    color: colors.grey[500]
  }
}));

const Reactions: FC<ReactionsProps> = ({ className, post, ...rest }) => {
  const classes = useStyles();
  const [isLiked, setLiked] = useState<boolean>(false);
  const [isBookmarked, setBookmarked] = useState<boolean>(true);
  const [likes, setLikes] = useState<number>(post.liked.count);

  const handleLike = (): void => {
    setLiked(true);
    setLikes(prevLikes => prevLikes + 1);
  };

  const handleUnlike = (): void => {
    setLiked(false);
    setLikes(prevLikes => prevLikes - 1);
  };

  const handleSaved = (): void => {
    setBookmarked(true);
  };

  const handleUnsaved = (): void => {
    setBookmarked(false);
  };

  return (
    <div className={clsx(classes.root, classes[className])} {...rest}>
      {isLiked ? (
        <Tooltip title="Unlike" disableTouchListener>
          <IconButton
            className={clsx(classes.likedButton, classes.likedIcon)}
            onClick={handleUnlike}
          >
            <CustomIcon src="/static/icons/trending_filled.svg" />
          </IconButton>
        </Tooltip>
      ) : (
        <Tooltip title="Like" disableTouchListener>
          <IconButton className={classes.likedIcon} onClick={handleLike}>
            <CustomIcon src="/static/icons/trending_outlined.svg" />
          </IconButton>
        </Tooltip>
      )}
      <Typography color="textSecondary" variant="h6">
        {likes}
      </Typography>
      <Box className={classes.commentBox}>
        <MessageCircleIcon className={classes.commentIcon} size="20" />
      </Box>
      <Typography color="textSecondary" variant="h6">
        {likes}
      </Typography>
      <Box className={classes.iconBox}>
        {isBookmarked ? (
          <Tooltip title="Unsaved" disableTouchListener>
            <IconButton className={classes.likedButton} onClick={handleUnsaved}>
              <BookmarkBorderOutlinedIcon fontSize="small" />
            </IconButton>
          </Tooltip>
        ) : (
          <Tooltip title="Saved" disableTouchListener>
            <IconButton onClick={handleSaved}>
              <BookmarkBorderOutlinedIcon fontSize="small" />
            </IconButton>
          </Tooltip>
        )}
      </Box>
      <Box className={classes.iconBox}>
        <IconButton>
          <ShareIcon fontSize="small" />
        </IconButton>
      </Box>
    </div>
  );
};

Reactions.propTypes = {
  className: PropTypes.string,
  // @ts-ignore
  post: PropTypes.object.isRequired
};

export default Reactions;
