import React, { FC, useState, useEffect } from 'react';
import { useHistory, useLocation, Link as RouterLink } from 'react-router-dom';
import parse from 'html-react-parser';
import moment from 'moment';
import {
  Paper,
  Box,
  Grid,
  Avatar,
  Typography,
  Chip,
  Divider,
  Link,
  useTheme,
  useMediaQuery,
  makeStyles,
  colors
} from '@material-ui/core';
import axios from 'src/utils/axios';
import { Theme } from 'src/theme';
import { Post, Tag } from 'src/types/post';
import Page from 'src/components/Page';
import Reactions from './Reactions';

const useStyles = makeStyles((theme: Theme) => ({
  root: {
    backgroundColor: theme.palette.background.dark,
    margin: '0 auto',
    width: '80%',
    minHeight: '100%',
    paddingTop: theme.spacing(3),
    paddingBottom: theme.spacing(3),
    [theme.breakpoints.down('sm')]: {
      width: '100%'
    }
  },
  chip: {
    marginLeft: theme.spacing(1),
    marginTop: theme.spacing(1)
  },
  box: {
    padding: theme.spacing(7),
    flexGrow: 1,
    [theme.breakpoints.down('sm')]: {
      padding: theme.spacing(3)
    }
  },
  author: {
    marginTop: theme.spacing(2),
    display: 'flex',
    alignItems: 'center'
  },
  avatar: {
    marginRight: theme.spacing(2)
  },
  content: {
    fontFamily: theme.typography.fontFamily,
    fontSize: theme.typography.pxToRem(17),
    marginBottom: theme.spacing(2),
    [theme.breakpoints.down('sm')]: {
      fontSize: theme.typography.pxToRem(17)
    }
  },
  disclosure: {
    fontFamily: theme.typography.fontFamily,
    '& p': {
      color: colors.grey[500],
      [theme.breakpoints.down('sm')]: {
        fontSize: '0.9rem'
      }
    }
  }
}));

const PostView: FC = () => {
  const classes = useStyles();
  const theme = useTheme();
  const history = useHistory();
  const location = useLocation();
  const matches = useMediaQuery(theme.breakpoints.down('sm'));
  const [post, setPost] = useState<Post>();
  const [title, setTitle] = useState<string>('Post');

  useEffect(() => {
    const fetchData = async (): Promise<void> => {
      const params = location.pathname.split('/')[2];
      const response = await axios.get<{ post: Post }>(`/posts/get/${params}`);

      if (response.data.post) {
        setPost(response.data.post);
        setTitle(response.data.post.title);
      } else {
        history.push('/404');
      }
    };
    fetchData();
  }, [location.pathname, history]);

  return (
    <Page className={classes.root} title={title}>
      <Paper>
        {post && (
          <Box mt={3} className={classes.box}>
            <Grid container spacing={3}>
              <Grid item xs={12} md={10}>
                <Typography variant="h1" component="header" color="textPrimary">
                  {post.title}
                </Typography>
                <Box className={classes.author}>
                  <Avatar
                    alt="Person"
                    component={RouterLink}
                    src={typeof post.author !== 'string' && post.author.avatar}
                    className={classes.avatar}
                    to="#"
                  />
                  <Typography variant="caption" color="textSecondary">
                    <Link
                      color="textSecondary"
                      component={RouterLink}
                      to="#"
                      variant="h6"
                    >
                      {typeof post.author !== 'string' && post.author.name}
                    </Link>
                    {` `}
                    {moment(post.createdAt).fromNow()}
                  </Typography>
                </Box>
                <Reactions post={post} className={!matches ? 'div' : ''} />
                <Box mt={2}>
                  {post.tags.map((tag: Tag) => (
                    <Chip
                      key={tag.symbol}
                      variant="outlined"
                      label={tag.symbol}
                      className={classes.chip}
                    />
                  ))}
                </Box>
              </Grid>
            </Grid>
            <Box mt={3} className={classes.content}>
              {parse(post.content)}
            </Box>
            <Divider />
            <Box mt={3} className={classes.disclosure}>
              {parse(post.disclosure)}
            </Box>
          </Box>
        )}
      </Paper>
    </Page>
  );
};

export default PostView;
